/* General styling */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
}

/* App container */
.app {
  text-align: center;
  padding: 20px;
}

/* Search bar styling */
.search-bar input {
  padding: 10px;
  width: 80%;
  margin: 20px 0;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

/* Movie list */
.movie-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); /* Each column has a fixed min size */
  gap: 20px; /* Consistent gap between movie boxes */
  padding: 0 10px;
  justify-items: center;
}

/* Individual movie item */
.movie-item {
  background-color: white;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 200px;
  height: 380px; /* Reduced height to decrease empty space */
  word-wrap: break-word;
  overflow: hidden;
}

/* Movie details */
.movie-details p:last-of-type {
  margin-bottom: 10px; /* Add spacing between ID and button */
}

/* Poster styling */
.movie-poster {
  width: 100%;
  height: 150px;
  object-fit: contain;
  border-radius: 5px;
  margin-bottom: 10px;
}

/* Movie details */
.movie-details {
  margin: 0;
  padding: 0;
  flex-grow: 1; /* Push the button to the bottom by taking up available space */
  text-align: left; /* Optional: Align text left if desired */
}

.movie-details h3 {
  font-size: 14px;
  color: #333;
  margin-top: 0;
  text-align: center;
}

.movie-details p {
  font-size: 12px;
  color: #777;
  margin: 4px 0;
  padding: 0; /* Ensure thereâ€™s no padding */
  text-align: center; /* Ensure text is centered */
  word-wrap: break-word;
  overflow: hidden;
}

/* Edit Button */
.edit-movie-button {
  padding: 8px 12px;
  background-color: #9caf97;
  color: black;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  width: 100%;
  max-width: 180px;
  position: absolute; /* Position the button at the bottom */
  bottom: 10px; /* 10px gap from the bottom */
  left: 50%; /* Center horizontally */
  transform: translateX(-50%); /* Center the button accurately */
  text-align: center;
  text-decoration: none; /* Ensure it's styled like a button */
}

.edit-movie-button:hover {
  background-color: #889c83;
}

/* Help page */
.help-search {
  text-align: center;
  margin-top: 20px;
}

/* AddMovieForm Styles */
.add-movie-form {
  background-color: white;
  padding: 30px;
  max-width: 500px;
  margin: 50px auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.add-movie-form h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.add-movie-form input,
.add-movie-form textarea {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
  background-color: #fafafa;
}

.add-movie-form input:focus,
.add-movie-form textarea:focus {
  border-color: #5a6158;
  outline: none;
}

.add-movie-form button {
  background-color: #9caf97;
  color: black;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  width: 100%;
}

.add-movie-form button:hover {
  background-color: #889c83;
}

/* Delete and Cancel Buttons */
.delete-button,
.cancel-button {
  padding: 10px 20px;
  background-color: #9caf97; /* Matching your other button color */
  color: black;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  text-decoration: none; /* Ensure it's styled like a button */
  margin: 5px; /* Small spacing between buttons */
}

.delete-button:hover,
.cancel-button:hover {
  background-color: #889c83; /* Matching hover effect */
}


.delete-movie-page {
  text-align: center;
  margin-top: 50px;
}

.delete-movie-page .movie-details {
  display: inline-block;
  background-color: #f4f4f4;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-bottom: 20px;
  width: 250px;
}

.delete-movie-page .movie-poster {
  width: 200px;
  height: 300px;
  margin-bottom: 15px;
  border-radius: 8px;
  object-fit: cover;
}

.delete-movie-page button {
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 10px;
}

.delete-movie-page button.delete {
  background-color: #9caf97;
  color: black;
}

.delete-movie-page button.cancel {
  background-color: #9caf97;
  color: black;
}

/* DeleteMovieList styles */
.delete-movie-list {
  text-align: center;
  gap: 20px;

}

.delete-movie-list .movie-item {
  display: inline-block;
  width: 250px;
  background-color: #f4f4f4;
  gap: 20px;
  padding: 20px;
  margin-bottom: 20px;
  text-align: center;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.delete-movie-list .movie-poster {
  width: 150px;
  height: 250px;
  border-radius: 8px;
  object-fit: cover;
  margin-bottom: 10px;
}

.delete-movie-list .delete-button {
  padding: 10px;
  background-color: #9caf97; /* Corrected button color */
  color: black;
  text-decoration: none;
  border-radius: 5px;
  display: inline-block;
}

.delete-movie-list .delete-button:hover {
  background-color: #889c83; /* Matching hover effect */
}

/* Specific to EditMoviePage */
.edit-movie-page {
  background: url('https://www.fourdayfireplace.com/wp-content/uploads/2022/12/Fireplace-safety-2.jpg') no-repeat center center fixed;
  background-size: cover;
  min-height: 100vh;
  padding: 20px;
  text-align: center;
}

/* Adding transparency to the form */
.edit-movie-page .add-movie-form {
  background-color: rgba(255, 255, 255, 0.8); /* White background with 80% opacity */
  padding: 30px;
  max-width: 500px;
  margin: 50px auto; /* Keeps the form positioned at the top, not centered */
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.edit-movie-page h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
}
